<script>
import { getDoc, getFirestore, deleteDoc, doc } from "firebase/firestore";

export default {
  components: {
  },
  data: () => {
    return {
      post: {
        title: "",
        content: "",
        id: "",
        img: '',
        time: ''
      },
      navlink: [],
      slug: "",
      editlink: "",
      count: null,
    };
  },
  created() {
    this.post.id = this.$route.params.slug;
    this.editlink = "/addpost/" + this.post.id;
    (async () => {
      const docSnap = await getDoc(doc(getFirestore(), "posts", this.post.id));
      if (docSnap.exists()) {
        var markdown = require("markdown").markdown;
        this.post.title = docSnap.data().title;
        this.post.content = markdown.toHTML(docSnap.data().mdcontent);
        var i = 0;
        this.post.content = this.post.content.replace(/(?:<h4>)/g, () => {
          i++;
          return '<h4 id="p' + i + '">';
        });
        this.post.id = docSnap.id;
        this.post.img = docSnap.data().img
        this.post.time = docSnap.data().time
      } else {
        console.log("No such document!");
      }
    })();
  },
  updated() {
    var id = 1;
    var biens = "";
    if (!this.count) {
      biens = document.getElementsByTagName("h4").length;
      this.count = 1;
    }

    while (id < biens + 1) {
      const nav = {
        name: "",
        link: "",
      };
      nav.name = document.getElementById("p" + id).innerText;
      nav.link = "#p" + id;
      this.navlink.push(nav);
      id++;
    }
  },
  methods: {
    xoa() {
      (async () => {
        await deleteDoc(doc(getFirestore(), "posts", this.post.id));
      })();
    },
  },
};
</script>

